import React, { useState } from "react";
import DefaultSetPasswordTemplate from "../components/templates/DefaultSetPasswordTemplate";

const SetNewPasswordPage = () => {
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [successMessage, setSuccessMessage] = useState<string | null>(null);

    const handleUpdatePassword = async (e: React.FormEvent) => {
        e.preventDefault();
        setError(null);
        setSuccessMessage(null);

        // Client-side validation
        if (password.length < 8) {
            setError("Password must be at least 8 characters long.");
            return;
        }

        if (password !== confirmPassword) {
            setError("Passwords do not match. Please try again.");
            return;
        }

        setLoading(true);
        console.log("[Auth] Updating password...");

        try {
            // Simulate API delay
            await new Promise((resolve) => setTimeout(resolve, 2000));

            // Simulation Logic
            setSuccessMessage("Your password has been successfully updated! You can now log in with your new credentials.");
            console.log("[Auth] Password updated successfully.");

        } catch (err: any) {
            setError(err.message || "Failed to update password. Please try again later.");
        } finally {
            setLoading(false);
        }
    };

    return (
        <DefaultSetPasswordTemplate
            password={password}
            confirmPassword={confirmPassword}
            onPasswordChange={setPassword}
            onConfirmPasswordChange={setConfirmPassword}
            onSubmit={handleUpdatePassword}
            loading={loading}
            error={error}
            successMessage={successMessage}
        />
    );
};

export default SetNewPasswordPage;
