import React from "react";
import "./DefaultSetPasswordTemplate.css";

export interface DefaultSetPasswordTemplateProps {
    password: string;
    confirmPassword: string;
    onPasswordChange: (value: string) => void;
    onConfirmPasswordChange: (value: string) => void;
    onSubmit: (e: React.FormEvent) => void;
    loading: boolean;
    error: string | null;
    successMessage: string | null;
}

const DefaultSetPasswordTemplate: React.FC<DefaultSetPasswordTemplateProps> = ({
    password,
    confirmPassword,
    onPasswordChange,
    onConfirmPasswordChange,
    onSubmit,
    loading,
    error,
    successMessage,
}) => {
    return (
        <div className="set-password-wrapper">
            <div className="set-password-card">
                <div className="set-password-header">
                    <div className="set-password-icon-box">
                        <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round">
                            <rect x="3" y="11" width="18" height="11" rx="2" ry="2" />
                            <path d="M7 11V7a5 5 0 0 1 10 0v4" />
                        </svg>
                    </div>
                    <h1 className="set-password-title">Set new password</h1>
                    <p className="set-password-subtitle">
                        Please choose a strong password to secure your account.
                    </p>
                </div>

                {successMessage && (
                    <div className="set-password-success">
                        <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round">
                            <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14" />
                            <polyline points="22 4 12 14.01 9 11.01" />
                        </svg>
                        <span>{successMessage}</span>
                    </div>
                )}

                {error && (
                    <div className="set-password-error">
                        <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                            <circle cx="12" cy="12" r="10" />
                            <line x1="12" y1="8" x2="12" y2="12" />
                            <line x1="12" y1="16" x2="12.01" y2="16" />
                        </svg>
                        <span>{error}</span>
                    </div>
                )}

                {!successMessage ? (
                    <form onSubmit={onSubmit} className="set-password-form">
                        <div className="form-group">
                            <label className="form-label">New Password</label>
                            <input
                                type="password"
                                className="set-password-input"
                                placeholder="••••••••"
                                value={password}
                                onChange={(e) => onPasswordChange(e.target.value)}
                                disabled={loading}
                                required
                            />
                        </div>

                        <div className="form-group">
                            <label className="form-label">Confirm New Password</label>
                            <input
                                type="password"
                                className="set-password-input"
                                placeholder="••••••••"
                                value={confirmPassword}
                                onChange={(e) => onConfirmPasswordChange(e.target.value)}
                                disabled={loading}
                                required
                            />
                        </div>

                        <button
                            type="submit"
                            className="set-password-button"
                            disabled={loading}
                        >
                            {loading ? (
                                <div className="loading-dots">
                                    <span></span><span></span><span></span>
                                </div>
                            ) : (
                                "Update Password"
                            )}
                        </button>
                    </form>
                ) : (
                    <div className="login-redirect">
                        <a href="/login" className="set-password-button" style={{ textDecoration: 'none' }}>
                            Go to Login
                        </a>
                    </div>
                )}

                <div className="login-redirect">
                    <a href="/login" className="login-link">
                        Back to Login
                    </a>
                </div>
            </div>
        </div>
    );
};

export default DefaultSetPasswordTemplate;
